// 
const rules = {
  // "/page/1?type=a#title"  "/page/1"
  path (route) {
    return route.path
  },

  //  (hash)"/page/1?type=a#title"  "/page/1?type=a"
  fullpath (route) {
    return route.fullPath.replace(route.hash, '')
  }
}

// 
export default {
  props: {
    // idroute
    aliveId: {
      type: [ String, Function ],
      default: 'path'
    }
  },

  methods: {
    //  id
    getAliveId (route = this.$route) {
      let rule = (route.meta && route.meta.aliveId) || this.aliveId

      if (typeof rule === 'string') {
        rule = rules[rule.toLowerCase()]
      }

      if (typeof rule !== 'function') {
        rule = rules.path
      }

      return rule.bind(this)(route)
    }
  }
}
